.App {
  max-width: 863px;
  min-width: 376px;
  margin: 40px auto;
  
}
.icon {
  margin-left: 13rem;
}

.to-top-light {
  cursor: pointer;
  border: none;
  color:#3A506B;
  padding-top:1.8em;
  display:inline-block;/* or block */
  position:relative;
  border-color:#3A506B;
  text-decoration: none;
  transition:all .3s ease-out;
}
.to-top-light:before{
  border: none;
  content:'▲';
  font-size:2em;
  position:absolute;
  top:0;
  left:50%;
  margin-left:-.7em;
  border:solid .13em white;
  border-radius:10em;
  width:1.4em;
  height:1.4em;
  line-height:1.3em;
  border-color:inherit;
  transition:transform .5s ease-in;
}
.to-top-light:hover{
  border:none;
  color:pink;
  border-color:pink;
}
.to-top-light:hover:before{
  transform: rotate(360deg);
}

.to-top-dark {
  cursor: pointer;
  background-color: transparent;
  border: none;
  color:white;
  padding-top:1.8em;
  display:inline-block;/* or block */
  position:relative;
  border-color:white;
  text-decoration: none;
  transition:all .3s ease-out;
}
.to-top-dark:before{
  border: none;
  content:'▲';
  font-size:2em;
  position:absolute;
  top:0;
  left:50%;
  margin-left:-.7em;
  border:solid .13em white;
  border-radius:10em;
  width:1.4em;
  height:1.4em;
  line-height:1.3em;
  border-color:inherit;
  transition:transform .5s ease-in;
}
.to-top-dark:hover{
  border:none;
  color:pink;
  border-color:pink;
}
.to-top-dark:hover:before{
  transform: rotate(360deg);
}


