.projects {
    max-width: 360px;
    margin: 60px auto;
    font-family: 'Lato', sans-serif;
}
.project-card {
    font-size: 1.2rem;
    padding: 5px;
    background-color: #FFE7D4;
    border-radius: 8px;
    box-shadow: 10px 10px 5px rgba(0,0,0,0.2);
    font-family: 'Lato', sans-serif;
}
.project-card p {
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    
}
.project-card-light {
    background-color: #5BC0BE;
    border: 1px solid #1C2541;
    border-radius: 20px;
    margin-bottom: .9rem;
}
.project-card-dark {
    background-color: #3A506B;
    border: 1px solid white;
    border-radius: 20px;
    margin-bottom: .9rem;
    text-align: center;
    color: white;
}
.project-card-dark a {
    text-decoration: none;
    color: white;
}
.project-card-dark a:visited {
    text-decoration: none;
    color: white;
}
.project-card-light a {
    text-decoration: none;
    color: #1C2541;
}

.project-card-light a:visited {
    text-decoration: none;
    color: #1C2541;
}
p {
    margin-top: -1rem;
    font-size: .9rem;
}
.down-arrow-dark {
    background: transparent;
    border: none;
    font-size: 2rem;
    color: white;
    cursor: pointer;
    animation: pulse 2s infinite;
}
.down-arrow-light {
    background: transparent;
    border: none;
    font-size: 2rem;
    color: #1C2541;
    cursor: pointer;
    animation: pulse 3s infinite;
}

@keyframes pulse {
    0%,
    100% {
      animation-timing-function: ease-in;
    }
    50% {
      transform: scale(1.33);
    }
  }
