.contact-form {
    position: absolute;
    
}
.contact-dark {
    max-width: 340px;
    margin: 0 auto 20px;
    background-color: #E6F4F1;
    border-radius: 40px;
    padding: 5px;
    box-shadow: 10px 10px 5px rgba(0,0,0,0.2);
}
.contact-dark h2 {
    color: #3A506B;
}
.contact-light {
    max-width: 340px;
    margin: 0 auto 20px;
    background-color: rgb(231, 228, 228);
    border-radius: 40px;
    border: 1px solid rgb(212, 212, 212);
    padding: 5px;
    box-shadow: 10px 10px 5px rgba(0,0,0,0.2);
}
.hide {
    display: none;
}

form { 
    max-width:420px; margin:50px auto; 
}

.contact-dark .feedback-input {
  margin: 10px;
  color:#1C2541;
  font-weight:500;
  font-size: 18px;
  border-radius: 20px;
  line-height: 22px;
  background-color: transparent;
  border:2px solid #3A506B;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width:19rem;
  box-sizing: border-box;
  outline:0;
}

.contact-light .feedback-input {
    margin: 10px;
    color:#1C2541;
    font-weight:500;
    font-size: 18px;
    border-radius: 20px;
    line-height: 22px;
    background-color: transparent;
    border:2px solid #5BC0BE;
    transition: all 0.3s;
    padding: 13px;
    margin-bottom: 15px;
    width:19rem;
    box-sizing: border-box;
    outline:0;
  }

.feedback-input:focus { border:2px solid #CC4949; }

textarea {
  height: 150px;
  line-height: 150%;
  resize:vertical;
}

.submit-contact-light {
    font-family: inherit;
    background: transparent;
    border: 1px solid #5BC0BE;
    padding: 5px;
    margin: 10px auto;
    border-radius: 8px;
}
.submit-contact-light:hover {
    background-color:#dfe2e6;
    cursor: pointer;
}
.submit-contact-dark {
    font-family: inherit;
    background: transparent;
    border: 1px solid #3A506B;
    padding: 5px;
    margin: 10px auto;
    border-radius: 8px;
}
.submit-contact-dark:hover {
    background-color:#dfe2e6;
    cursor: pointer;
}
.sent {
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    color: gray;
    margin: 20px auto;
    transition: 2s ease-in;
}

