.about-me {
    max-width: 300px;
    margin: 0 auto;
    text-align: center;
    position: flex;
    justify-content: center;
}
.light-languages, .light-education {
    background-color: #5BC0BE;
    border: 1px solid #3A506B;
    border-radius: 20px;
    margin-bottom: 2rem;
    text-align: center;
    
}
.dark-languages, .dark-education {
    background-color: #3A506B;
    border: 1px solid white;
    border-radius: 20px;
    margin-bottom: 2rem;
    text-align: center;
}
.light-certifications, .light-certifications {
    background-color: #5BC0BE;
    border: 1px solid #3A506B;
    border-radius: 20px;
    margin-bottom: 2rem;
}
.dark-certifications, .dark-certifications {
    background-color: #3A506B;
    border: 1px solid white;
    border-radius: 20px;
    margin-bottom: 2rem;
    text-align: center;
}
.wrapper {
    text-align: center;
    font-size: 1.1rem;
    padding-bottom: 1rem;
    box-shadow: 10px 10px 5px rgba(0,0,0,0.2);
    padding: .3rem;
}
.wrapper ul {
    list-style-type: none;
    display: inline-block;
    margin: 0;
    padding: 0;
}
.wrapper a {
    text-decoration: none;
    color: white;
}
.wrapper a:visited {
    text-decoration: none;
    color: white;
}

.wrapper h4 {
    font-weight: 600;
}
.wrapper h3 {
    font-size: 1.3rem;
}
.ua { 
    max-width: 100px;
}
.wrapper h5 {
    margin-top: -1.5rem;  
    font-weight: 300; 
}
.wrapper h2 {
    display: inline;
    font-size: 1.3rem;
}
.wrapper h1 {
    font-size: 1rem;
    
}

