.header {
    line-height: 3.5rem;
    margin: auto;
    max-width: 480px;
    margin-bottom: 21rem;
}
.header h1 {
    font-size: 2rem;
}
.header h6 {
    margin-bottom: -2rem;
}
.header h5 {
    margin-top: -2rem;
    opacity: 0.3;
}
.buttons {
    max-width: 380px;
    margin: 1rem auto;
    display: flex;
    gap: 10px;
    justify-content: center;
    font-family : inherit;
    
}
.dark-button {
    background: none;
    border: 2px solid #fff;
    padding: 16px 12px;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: cell;
    font-size: .8em;
    transition: all ease-in 0.2s;
    font-family : inherit;
}
.dark-button:hover {
    color: #fff;
    background: #3A506B;
}
.light-button {
    background: none;
    border: 2px solid #1C2541;
    padding: 16px 12px;
    border-radius: 4px;
    color: #1C2541;
    font-weight: bold;
    cursor: cell;
    font-size: .8em;
    font-family : inherit;
    transition: all ease-in 0.2s;
}
.light-button:hover {
    background: #5BC0BE;
    color: white;
    border: 2px solid #1C2541;
}
.me-noback {
    margin-bottom: -19.1px;
    margin-top: 5rem;
    max-width: 200px;
}
.img-bkg {
    background-color: #3A506B;
    max-width: 250px;
    margin: 100px auto;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    margin-bottom: -10px;
    box-shadow: 15px 15px 15px rgba(0,0,0,0.2);
}
.light .img-bkg{
    background-color: #5BC0BE;
}
.dark .img-bkg{
    background-color: #3A506B;
}
.down {
    flex-wrap: wrap;
}