@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400&display=swap');

/* base styles */
body {
  background-color: #1C2541; 
  font-family: 'Lato', sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  color: white;
}
.dark {
  background-color: #1C2541; 
  font-family: 'Lato', sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  color: white;
  transition: all ease-in 0.3s;
}
.light {
  background-color: #eee; 
  font-family: 'Lato', sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  color: #1c2541;
  transition: all ease-in 0.3s;
}
